<template>
  <transition name="fade">
    <div id="app">
      <div class="container">
        <h1>TOTP Token Generator</h1>
        <div class="form-item">
          <label for="secret_key"> YOUR SECRET KEY: </label>

          <input
            name="secret_key"
            v-model="secret_key"
            placeholder="The secret key (in base-32 format)"
            @keyup.enter="updateSecretKey"
          />
        </div>
        <button
          :disabled="!secret_key || !secret_key.length"
          @click="updateSecretKey"
        >
          Update
        </button>

        <div v-if="totp" class="totp">
          {{ totp }}
        </div>

        <div class="timer" v-if="timer">Time update in: {{ timer }}s</div>
      </div>
    </div>
  </transition>
</template>

<script>
import Vue from "vue";
//import Notifications from "vue-notification";

//Vue.use(Notifications);
const CryptoJS = require("crypto-js");
const jsotp = require("jsotp");

import { bus } from "./main";

export default {
  name: "app",
  data() {
    return {
      secret_key: "",
      keyOTP: null,
      totp: null,
      timer: null,
      interval: null,
    };
  },
  created() {
    bus.$on("updateValues", this.checkTotp);
    window.onfocus = function() {
      bus.$emit("updateValues");
    };
  },
  beforeDestroy() {
    bus.$off("updateValues", this.checkTotp);
  },
  methods: {
    checkTotp() {
      if (this.secret_key && this.secret_key.length) this.getOTP();
    },

    updateSecretKey() {
      if (!this.secret_key || !this.secret_key.length) return;
      const secretKeyType = "secret key 123";

      // Decrypt
      let bytes = CryptoJS.AES.decrypt(this.secret_key, secretKeyType);
      this.keyOTP = bytes.toString(CryptoJS.enc.Utf8);
      this.getOTP();
    },
    async updateTotp() {
      const totp = await jsotp.TOTP(this.keyOTP);
      this.totp = totp.now();
    },
    async timeLoop() {
      let epoch = Math.round(new Date().getTime() / 1000.0);
      let countDown = 30 - (epoch % 30);
      this.timer = countDown;
      if (epoch % 30 == 0) await this.updateTotp();
    },
    getOTP() {
      this.interval = null;
      this.updateTotp();

      this.interval = setInterval(this.timeLoop, 1000);
    },
  },
};
</script>

